import router from "./router/index"
router.beforeEach((to, form, next) => {
  console.log();
  if (to.path === '/login') {
    next();
  } else {
    let token = localStorage.getItem('token') || []
    if (token === '登入') {
      next();
    } else {
      next('/login')
    }
  }
})
export default router