import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import(/* webpackChunkName: "home" */ '../views/home/index.vue'),
    meta: {
      title: "首页",
      keepAlive: false
    },

  },
  {
    path: '/login',
    name: 'login',
    component: () => import(/* webpackChunkName: "home" */ '../views/login/index.vue'),
    meta: {
      title: "登录",
      keepAlive: false
    },

  },
  {
    path: '/elementUiApi',
    name: 'elementUiApi',
    component: () => import(/* webpackChunkName: "date" */ '../views/develop/elementUiApi/index.vue'),
    meta: {
      title: "elementUiApi",
      keepAlive: true,
      childrens: true,
    },
    children: [
      {
        path: '/elementUiApi/date',
        name: 'date',
        component: () => import(/* webpackChunkName: "date" */
          "../views/develop/elementUiApi/childrenpath/date/index.vue"),
        meta: {
          title: "elm弹窗",
          keepAlive: true,
          childrens: false,
        },
      },
      {
        path: '/elementUiApi/delog',
        name: 'delog',
        component: () => import(/* webpackChunkName: "delog" */ '../views/develop/elementUiApi/childrenpath/delog/index.vue'),
        meta: {
          title: "elm日期控件",
          keepAlive: true,
          childrens: false,
        },

      },
      {
        path: '/elementUiApi/regular',
        name: 'regular',
        component: () => import(/* webpackChunkName: "regular" */ '../views/develop/elementUiApi/childrenpath/regular/index.vue'),
        meta: {
          title: "elm正则",
          keepAlive: true,
          childrens: false,
        },
      },
    ]
  },

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
